import type { DomainLabel } from "@RpgTypes/templates";
import type {
  CollapsOptionLabels,
  ExtraParamLabels,
  PartyAbilityOptionLabels,
  RegularParamLabels,
  SpecialFlagOptions,
  SpecialParamLabels,
  RawTraitLabel,
  TraitLabelWithOption,
} from "./items";
import type { TraitLabelSet } from "./labelSet";

const FORMAT_NAME = "{name}" as const;
const FORMAT_PARAM_RATE_MUL = "{name} * {value}%" as const;
const FORMAT_PARAM_RATE_PLUS = "{name} + {value}%" as const;
const FORMAT_PARAM_VALUE = "{value}" as const;

export const LABEL_SET_TRAIT = {
  domainName: "",
  options: {
    regularParam: {
      domainName: "",
      format: FORMAT_PARAM_RATE_MUL,
      options: {
        maxHp: "HP",
        maxMp: "MP",
        atk: "",
        def: "",
        matk: "",
        mdef: "",
        agi: "",
        luk: "",
      },
    } as const satisfies TraitLabelWithOption<RegularParamLabels>,
    extraParam: {
      domainName: "",
      format: FORMAT_PARAM_RATE_PLUS,
      options: {
        hitRate: "",
        evasionRate: "",
        criticalRate: "",
        criticalEvasionRate: "",
        magicEvasionRate: "",
        magicReflectionRate: "",
        counterAttackRate: "",
        hpRegenerationRate: "HP",
        mpRegenerationRate: "MP",
        tpRegenerationRate: "TP",
      },
    } as const satisfies TraitLabelWithOption<ExtraParamLabels>,
    specialParam: {
      domainName: "",
      format: FORMAT_PARAM_RATE_MUL,
      options: {
        targetRate: "",
        guardEffectRate: "",
        recoveryEffectRate: "",
        pharmacology: "",
        mpCostRate: "MP",
        tpChargeRate: "TP",
        physicalDamageRate: "",
        magicDamageRate: "",
        floorDamageRate: "",
        experienceRate: "",
      },
    } as const satisfies TraitLabelWithOption<SpecialParamLabels>,
    specialFlag: {
      domainName: "",
      format: FORMAT_NAME,
      options: {
        autoBattle: "",
        guard: "",
        substitute: "",
        preventEscape: "",
      },
    } as const satisfies TraitLabelWithOption<SpecialFlagOptions>,
    collaps: {
      domainName: "",
      format: FORMAT_NAME,
      options: {
        bossCollaps: "",
        instantCollaps: "",
        noneCollaps: "",
      },
    } as const satisfies TraitLabelWithOption<CollapsOptionLabels>,
    partyAbility: {
      domainName: "",
      format: FORMAT_NAME,
      options: {
        cancelSurprise: "",
        dropItemDouble: "2",
        encounterHalf: "",
        encounterNone: "",
        goldDouble: "2",
        raisePreemptive: "",
      },
    } as const satisfies TraitLabelWithOption<PartyAbilityOptionLabels>,
    elementRate: {
      domainName: "",
      format: FORMAT_PARAM_RATE_MUL,
    } as const satisfies RawTraitLabel,
    debuffRate: {
      domainName: "",
      format: FORMAT_PARAM_RATE_MUL,
    } as const satisfies RawTraitLabel,
    stateRate: {
      domainName: "",
      format: FORMAT_PARAM_RATE_MUL,
    } as const satisfies RawTraitLabel,
    stateResist: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    attackElement: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    attackState: {
      domainName: "",
      format: FORMAT_PARAM_RATE_PLUS,
    } as const satisfies RawTraitLabel,
    attackSpeed: {
      domainName: "",
      format: FORMAT_PARAM_VALUE,
    } as const satisfies RawTraitLabel,
    attackTimes: {
      domainName: "",
      format: FORMAT_PARAM_VALUE,
    } as const satisfies RawTraitLabel,
    actionPlus: {
      domainName: "",
      format: FORMAT_PARAM_RATE_MUL,
    } as const satisfies RawTraitLabel,
    attackSkill: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    equipWeaponType: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    equipArmorType: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    equipLock: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    equipSeal: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    slotType: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    skillAdd: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    skillSeal: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    skillTypeAdd: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
    skillTypeSeal: {
      domainName: "",
      format: FORMAT_NAME,
    } as const satisfies RawTraitLabel,
  },
} as const satisfies DomainLabel<TraitLabelSet>;
